'use strict';

import angular from 'angular';
import urlService from 'common/services/url.service';

const homeCtrlModule = angular.module('home.controller.js', [
  urlService.name
]);

class HomeCtrl {

  // called once when the class is instantiated
  constructor ($scope, URL) {
    this.$scope = $scope;
    this.URL = URL;
  }

  // public class methods
  deleteThisFunction () {
    // does nothing
  }
}

homeCtrlModule.controller('HomeCtrl', ['$scope', 'URL', HomeCtrl]);

export default homeCtrlModule;
